import {AfterViewInit, Component, OnDestroy, ElementRef, OnInit, ViewChild} from '@angular/core';
import {
  GoogleMaps,
  GoogleMap,
  GoogleMapsEvent,
  LatLng,
  MarkerOptions,
  Marker
} from "@ionic-native/google-maps";
import { Platform,AlertController } from '@ionic/angular';
import { AuthenticationService } from '../api/authentication.service';
import { Router } from '@angular/router';
import { FCM } from '@ionic-native/fcm/ngx';
declare var google;

@Component({
  selector: 'app-geolocation',
  templateUrl: './geolocation.page.html',
  styleUrls: ['./geolocation.page.scss'],
})
export class GeolocationPage implements OnInit,AfterViewInit {
  latitude: any;
  longitude: any;
  servicios = ['12121','12234','00121','010121','121212','12121','12234','20121','514131','124232'];
  ishidden = false;
  backButtonSubscription; 

  @ViewChild('mapElement', {static: true}) mapNativeElement: ElementRef;
  constructor(
    public alertController: AlertController,
    public authService: AuthenticationService,
    private platform: Platform,
    private router: Router,
    private fcm: FCM
    ) { 
      this.fcm.getToken().then(token => {
        console.log(token);
        this.authService.getUrlClientPost("/cliente/update",{"fcm":token})
                    .then(data =>{
                      console.log(data);
                    });
      });
      this.fcm.onNotification().subscribe(data => {
        console.log(data);
        if (data.wasTapped) {
          console.log('Received in background');
        } else {
          console.log('Received in foreground');
        }
        this.router.navigate([data.landing_page, data.servicio]);
      });
    }

  ngOnInit() {
    this.authService.getUrlClientGet("/cliente",{})
                    .then(data =>{
                      console.log(data);
                    });
                    
    
  }

  ngAfterViewInit(): void {
    this.platform.ready().then( () => {

			this.loadMap();
		});
  }

async presentAlert() {
    let serv ={};
    serv["tipo"] = "";
    serv["seccionFalla"] = "";
    serv["frecuenciaFalla"] = "";
    serv["equipo"] = "";
    serv["descripcion"]="";
    serv["status"]="Nuevo";
    serv["statusEquipo"] ="";
    serv["plataforma"] = "";
     this.authService.getUrlClientPost("/servicio/add",{"servicio" : serv})
                    .then(data =>{
                      
                    });
    const alert = await this.alertController.create({
      header: 'Solicitud enviada',
      message: 'Su solicitud ha sido creada, gracias',
      buttons: ['Enterado']
    });

    await alert.present();
  }

  async presentAlertPrompt() {
    const alert = await this.alertController.create({
      header: 'Para continuar necesitas registrarte',
      inputs: [
        {
          name: 'name1',
          type: 'text',
          placeholder: 'Nombre Completo'
        },
        {
          name: 'name1',
          type: 'text',
          placeholder: 'Correo'
        },
        {
          name: 'name2',
          type: 'number',
          placeholder: 'Celular'
        }
      ],
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            console.log('Confirm Cancel');
          }
        }, {
          text: 'Registrar',
          handler: () => {
            console.log('Confirm Ok');
          }
        }
      ]
    });

    await alert.present();
  }

  async clickServicio(servicio) {
      console.log("es un servicio " + servicio);
  }

  loadMap() {

	let map = GoogleMaps.create( 'mapElement' );

	map.one( GoogleMapsEvent.MAP_READY ).then( ( data: any ) => {

		let coordinates: LatLng = new LatLng( 36.7783, 119.4179 );

		let position = {
			target: coordinates,
			zoom: 14
		};

		map.animateCamera( position );

		let markerOptions: MarkerOptions = {
			position: coordinates,
			icon: "assets/icon/logopqno.png",
			title: 'Hello California'
		};

		const marker = map.addMarker( markerOptions )
		.then( ( marker: Marker ) => {
			marker.showInfoWindow();
		});
	})
}

}